* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  color: #fff;
}

body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 300px;
  min-width: 300px;
  width: 33%;

  flex-grow: 1;
}

.block--1 {
  background-color: #f00;
  order: 1;
}

.block--2 {
  background-color: #c00;
  order: 2;
}

.block--3 {
  background-color: #900;
  order: 3;
}

.block--4 {
  background-color: #600;
  order: 6;
}

.block--5 {
  background-color: #300;
  order: 5;
}

.block--6 {
  background-color: #000;
  order: 4;
}

@media (min-width: 600px) and (max-width:899px) {
  .block--4 {
    order: 3;
  }

  .block--3 {
    order: 4;
  }

  .block--6 {
    order: 6;
  }
}

@media (min-width: 300px) and (max-width:599px) {
  .block--3 {
    order: 3;
  }

  .block--4 {
    order: 4;
  }

  .block--6 {
    order: 6;
  }
}
